/*  03. NAVBAR
/*-----------------------------------------------------------------------------------*/
.navbar {
    z-index: 9998;
    border: 0;
    border-radius: 0;
    min-height: inherit;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    height: 90px;
    background: rgba(100,58,144,0.8) !important;  
    transition: all 0.3s;
    font-family: 'Lato';
}
.container>.navbar-header {
    margin-left: 0 !important;
    margin-right: 0 !important;
}
.navbar .navbar-brand {
    padding: 0;
    float: none;
    height: auto;
    position: relative;
    box-shadow: none;
    margin: 0 !important;  
}
.navbar .navbar-brand a {
    transition: none;  
}
.navbar .navbar-collapse {
    padding-right: 0;
    padding-left: 0;
    float: none;
    text-align: center;
    width: 100%;   
}
.navbar .navbar-nav {
    float: none;  
    pointer-events: none;
}
.navbar .navbar-nav > li {
    float: none;
    pointer-events: all;
}
.navbar .navbar-nav > li > a {
    padding: 0;
    font-size: 12px;
    font-weight: 600;
    transition: all 150ms ease-in;
    text-transform: uppercase;
    line-height: 1;
    color: #FED037 !important;
    position: relative;  
}
.navbar.light .navbar-nav > li > a,
.navbar.light .btn.responsive-menu {
    color: #3b3b3b !important;
}
.navbar .navbar-nav > li > a:focus,
.navbar .nav > li > a:hover,
.navbar .nav > li.current > a {
    background: none; 
}
.navbar .nav > li.current > a,
.navbar .navbar-nav > li > a:focus,
.navbar .nav > li > a:hover {
    color: #fff !important; 
}
.navbar .dropdown-menu {
    padding: 0;
    margin: 0;
    min-width: 150px;
    border: none;
    background: none;
    border-radius: 0;
    box-shadow: none;
    padding-top: 15px;   
}
.navbar .dropdown-menu li {
    background: #26262a;
    border: none;
    border-top: 1px solid rgba(255,255,255,0.08);
}
.navbar .dropdown-menu li:first-child {
    border-top: 2px solid #fed038
}
.navbar .dropdown-menu li a {
    background: none;
    border: none;
    color: #fff;
    font-size: 11px;
    font-weight: 600;
    /*letter-spacing: 0;
    */text-transform: uppercase;
    padding: 15px 20px 15px;
    line-height: 1;
}
.navbar .dropdown-menu li a:hover,
.navbar .dropdown-menu li a.active {
    padding: 15px 20px 15px;
    filter: none;
    line-height: 1;
}
.navbar .dropdown-menu li a:hover,
.navbar .dropdown-menu li a.active,
.navbar .nav .open > a,
.navbar .nav .open > a:hover,
.navbar .nav .open > a:focus,
.navbar .dropdown-menu > li > a:hover,
.navbar .dropdown-menu > li > a:focus,
.navbar .dropdown-submenu:hover > a,
.navbar .dropdown-submenu:focus > a,
.navbar .dropdown-menu > .active > a,
.navbar .dropdown-menu > .active > a:hover,
.navbar .dropdown-menu > .active > a:focus {
    background: none;
    color: #fed038;
}
.navbar .nav > li > .dropdown-menu:after {
    display: none
}
.navbar .dropdown-menu > li > a:hover,
.navbar .dropdown-menu > li > a:focus,
.navbar .dropdown-submenu:hover > a,
.navbar .dropdown-submenu:focus > a,
.navbar .dropdown-menu > .active > a,
.navbar .dropdown-menu > .active > a:hover,
.navbar .dropdown-menu > .active > a:focus {
    filter: progid:DXImageTransform.Microsoft.gradient(enabled = false)
}
.caret {
    border-top: 4px solid
}
/* Responsive Menu Button */
.btn.responsive-menu {
    padding: 0;
    margin: 0;
    display: none;
    text-align: center;
    font-size: 22px;
    background: none;
    position: relative;
    z-index: 999999;
